import React, { useState } from "react";
import { Menu, Badge, Input } from "antd";
import {
  LogoutOutlined,
  UserOutlined,
  UserAddOutlined,
  CaretDownOutlined,
  ShoppingCartOutlined,
  SearchOutlined
} from "@ant-design/icons";
import { Link } from "react-router-dom";
// for logout
import { auth } from "../../firebase";
import { useDispatch, useSelector } from "react-redux";
import { useHistory } from "react-router-dom";
const { SubMenu, Item } = Menu;
const { Search } = Input;

const Header = () => {
  const [current, setCurrent] = useState("home");
  const [globalSearch, setGlobalSearch] = useState('')

  const history = useHistory();
  const dispatch = useDispatch();
  const { user, cart } = useSelector((state) => ({ ...state }));
  
  const globalSearchHistory =()=>{
    if (globalSearch!=='') {
      console.log('global')
      
      history.push(`/catalog/globalSearch/${globalSearch}`)
      history.go(0)
    }
  }
  
  
  const handleClick = (e) => {
    setCurrent(e.key);
  };
  // logout function
  const logout = () => {
    auth.signOut();
    dispatch({
      type: "LOGOUT",
      payload: null,
    });
    dispatch({
      type: "ADD_TO_CART",
      payload: [],
    });
    dispatch({
      type: "SET_DISCOUNT",
      payload: false,
    });
    history.push("/");
  };

  return (
    <div>
      <Menu onClick={handleClick} selectedKeys={[current]} mode="horizontal">
        <Menu.Item key="home">
          <Link to="/"></Link>
        </Menu.Item>

        {!user && (
          <Item
            key="registration"
            icon={<UserAddOutlined />}
            className="float-right"
          >
            <Link to="/register"></Link>
          </Item>
        )}

        {!user && (
          <Item key="login" icon={<UserOutlined />} className="float-right">
            <Link to="/login"></Link>
          </Item>
        )}

        {user && (
          <SubMenu
            key="SubMenu"
            icon={<CaretDownOutlined />}
            title={user.email && user.email.split("@")[0]}
            className="float-right"
          >
            <Menu.ItemGroup>
              {user && user.role === "admin" && (
                <Item key="adminDashboard">
                  <Link to="/admin/dashboard"></Link>
                </Item>
              )}
              {user && user.role === "subscriber" && (
                <Item key="adminDashboard">
                  <Link to="/user/dashboard"> </Link>
                </Item>
              )}
              {user && user.role === "manager" && (
                <Item key="adminDashboard">
                  <Link to="/manager/dashboard"> </Link>
                </Item>
              )}
              <Item icon={<LogoutOutlined />} onClick={logout}>
                
              </Item>

            </Menu.ItemGroup>
          </SubMenu>
        )}
        <Item key="cart" className="float-right" style={{ display: "flex", alignItems: "center" }}>
          <Link to="/user/cart">
            <Badge
              count={cart.length}
            // style={{ cursor: "pointer" }}
            // className="m-3"
            >
              <ShoppingCartOutlined
                style={{ fontSize: "1rem" }}
              /> 
                  </Badge>
          </Link>
        </Item>
        {/*   ---------------------------------------------------------------  */}
        <Item
          key="search" className="float-right" style={{ display: "flex", alignItems: "center", paddingTop: "5px" }}
        >


          <Search enterButton="" bordered="false" value={globalSearch} onChange={e => setGlobalSearch(e.target.value)} onSearch={() => globalSearchHistory()} />
          {/* <input style={{ maxWidth: "150px", border:"none" }} value={globalSearch} onChange={e=>setGlobalSearch(e.target.value)}  />  */}

        </Item>
      </Menu>
    </div>
  );
};

export default Header;
